import React, { useContext, useEffect } from 'react';
import './profile.css';
import { GlobalContext } from '../../context/globalState';

const Profile = () => {
    const { user, token, getUser, getRefreshToken } = useContext(GlobalContext)

    useEffect(() => {

        async function render() {
            await getRefreshToken()
        }
        render();

        if (token !== null) {
            getUser(token);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const { images, name, bio, email } = user;

    return (
        <>
            <div class="container" style={{padding: "80px"}}>
                <div class="row">
                    <div class="col-lg-5 col-md-6">
                        <div class="mb-2">
                            <img class="w-100" src="https://bootdey.com/img/Content/avatar/avatar7.png" alt=""/>
                        </div>
                        <div class="mb-2 d-flex">
                            <h4 class="font-weight-normal">{name}</h4>
                        </div>
                        <div class="mb-2">
                            <ul class="list-unstyled">
                                <li class="media">
                                <span class="w-25 text-black font-weight-normal">Email: </span>
                                <label class="media-body">{email}</label>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-lg-7 col-md-6 pl-xl-3">
                        <h3 class="dark-color">Bio</h3>
                        <h4 class="theme-color">UI / UX Designer &amp; Web Developer</h4>
                        <p>{bio}</p>  
                        <div class="btn-bar">
                            <a class="px-btn theme" href="#">View Works</a>
                            <a class="px-btn theme-t" href="#">Download CV</a>
                        </div>                      
                    </div>
                </div>
            </div>
        </>
    )
}

export default Profile;